import { Wrapper, Filterlabel, FilterInput } from './ContactsFilter.styled';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import phoonebookActions from '../../redux/phonebook/phoonebook-actions';

const ContactsFilter = ({ filter, hendleFinder }) => {
  return (
    <Wrapper>
      <Filterlabel>
        Find
        <FilterInput
          type="text"
          name="message"
          value={filter}
          onChange={hendleFinder}
        />
      </Filterlabel>
    </Wrapper>
  );
};

const mapStateToProps = state => ({
  filter: state.phoneBook.filter,
});

const mapDispatchToProps = dispatch => ({
  hendleFinder: e =>
    dispatch(phoonebookActions.changeFilter(e.currentTarget.value)),
});

export default connect(mapStateToProps, mapDispatchToProps)(ContactsFilter);

ContactsFilter.propTypes = {
  filter: PropTypes.string,
  hendleFinder: PropTypes.func,
};
